<template>
  <div id="app">
    <transition name="router-fade" mode="out-in">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<style type="text/css">
html,body,#app{height: 100%;}
.router-fade-enter-active, .router-fade-leave-active {
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.row{display: flex;}
.row-center{display: flex;justify-content: center;}
.model-input{line-height: 33px;margin-left: 10px;width: auto;}
/*.model-input .name{display: inline-block;float: left;}*/
.input{width: 160px !important;}
.button{margin: 0 5px !important;}
/*选中的字体颜色*/
.select-color{color: #00a8d4;}
/*标题的文字颜色*/
.title-color{color: #333;}
/*内容的文字颜色*/
.content-color{color: #666;}
/*弱提示文字颜色*/
.msg-color{color: #999;}
/*强提示文字颜色*/
.max-msg-color{color: #e84907;}
/*导航栏的背景颜色*/
.nav-bg{background: #1c2327;}
/*导航栏选中的时候的背景颜色*/
.select-nav-bg{background: #00a8d4;}
</style>

<script>
import store from '@/store'
export default {
  name: 'App',
  data() {
    return {
      title: 'GPS监控管理平台',
      msg: '记住密码？',
      login: '登录',
      leftHide: true,
      loginIsYes: true,
      addClass: '1',
      navList: [
      {
        text: '系统管理'
      },
      {
        text: '测试功能'
      }
      ],
      ruleForm: {
        userName: '', //用户名
        password: ''  //密码
      }
    }
  },
  created() {
    var d = new Date();
    d.setTime(d.getTime()+(1*60*60*1000));
    var expires = "expires="+d.toGMTString();
    document.cookie = "cookieName;" + expires;
  },
  mounted(){//地图需要在dom节点加载完毕开始渲染
  // var map = new BMap.Map('app');
  // var point = new BMap.Point(108.840053, 34.129522);
  // map.centerAndZoom(point, 10);
  // map.enableScrollWheelZoom();
  // this.drawLine();
  // this.loadBMapScript();
  /*window['bMapInit'] = () => {
    this.qeuryLocation();
    var map = new BMap.Map('app');
    var point = new BMap.Point(108.840053, 34.129522);
      map.centerAndZoom(point, 10);
      map.enableScrollWheelZoom();
    };*/
  },
  methods: {
    loadBMapScript () {
      let script = document.createElement('script');
      script.id = 'script';
      script.src = 'http://api.map.baidu.com/api?v=2.0&ak=dF8sMa2zZUk8znVfxyvzxQjsiQWrDfW2&callback=bMapInit';
      document.body.appendChild(script);
    },
    qeuryLocation () {
      let myGeo = new BMap.Geocoder();
      // 地址转换成坐标系
      myGeo.getPoint('北京市海淀区上地10街10号', function (point) {
        if (point) {
          
        }
      },
      '北京市');
    },

    english() {
      this.title = 'GPS monitoring platform';
      this.msg = 'Remember password?';
      this.login = 'Login';
      this.leftHide = false;
    },
    chinese() {
      this.title = 'GPS监控管理平台';
      this.msg = '记住密码？';
      this.login = '登录';
      this.leftHide = true;
    },
    loginFn() {
      this.loginIsYes = false;
    },
    changeBorder(index) {
      this.addClass = index;
    },
    drawLine(){
      // 基于准备好的dom，初始化echarts实例
      var Oapp = document.getElementById('app');
      let myChart = this.$echarts.init(Oapp);
      var div = document.createElement('div');
      // Oapp.appendChild(div).innerHTML = '原生js';
      Oapp.prepend(div);
      // 绘制图表
      myChart.setOption({
        title: { text: '在Vue中使用echarts' },
        tooltip: {},
        xAxis: {
          data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20],
          barMaxWidth:30,//最大宽度
          itemStyle:{
            normal:{
              color:'#4ad2ff'
            }
          }  
        }]
      });
    }
  },
  watch:{
    $route(to,from){//监听路由
      var routeName = to.name;
    }
  },
  store
}
</script>


