const Home = ()=> import('../page/home')
const Login = ()=> import('../page/login')
const TestF = ()=> import('../page/testF/testF')

//车辆管理
const CarManage = ()=> import('../page/home/carManag')
const CarManagSub = ()=> import('../page/home/carManag/carManagSub')
const DriverManag = ()=> import('../page/home/carManag/driverManag')
//首页
const HomePage = ()=> import('../page/home/homePage')
//车辆监控
const AllMonitor = ()=> import('../page/home/allMonitor')
const CarAnalysis = ()=> import('../page/home/carAnalysis')
//运营分析
const OAnalysis = ()=> import('../page/home/OAnalysis')
//数据分析
const DataSearch = ()=> import('../page/home/dataSearch')
const ParkInfo = ()=> import('../page/home/dataSearch/parkInfo')
//const Login = r => require.ensure([], () => r(require('../page/login/login')), 'login')
//系统管理
const SysetmM = ()=> import('../page/home/systemM')
const UserManage = ()=> import('../page/home/systemM/userManage')
const DivisionManage = ()=> import('../page/home/systemM/divisionManage')
const RoleManage = ()=> import('../page/home/systemM/roleManage')
//菜单管理
const MenuManage = ()=> import('../page/home/systemM/menuManage/menuManage')
const ButtonManage = ()=> import('../page/home/systemM/menuManage/buttonManage')
const ModelManage = ()=> import('../page/home/systemM/menuManage/modelManage')


export default [
{
  path: '/',
  redirect: '/home'
},
{
  path:'/home',
  name:'/home',
  meta: {
      requireAuth: true  // 添加该字段，表示进入这个路由是需要登录的
    },
    component: Home,
    children: [
    {
      path: '/home/homePage',
      name: '/home/homePage',
      meta: {
        requireAuth: true
      },
      component: HomePage
    },
    {
      path: '/home/carMonitor/allMonitor',
      name:'/home/carMonitor/allMonitor',
      meta: {
        requireAuth: true
      },
      component: AllMonitor
    },
    {
      path: '/home/carMonitor/carAnalysis',
      name:'/home/carMonitor/carAnalysis',
      meta: {
        requireAuth: true
      },
      component: CarAnalysis
    },
    {
      path: '/home/carManag',
      name:'/home/carManag',
      meta: {
        requireAuth: true
      },
      component: CarManage,
      children: [
      {
        path: '/home/carManag/carManagSub',
        name:'/home/carManag/carManagSub',
        meta: {
          requireAuth: true
        },
        component: CarManagSub
      },
      {
        path: '/home/carManag/driverManag',
        name:'/home/carManag/driverManag',
        meta: {
          requireAuth: true
        },
        component: DriverManag
      },
      {
       path: '/home/carManag',
       redirect: '/home/carManag/driverManag'
     }
      ]
    },
    {
      path: '/home/OAnalysis',
      name:'/home/OAnalysis',
      meta: {
        requireAuth: true
      },
      component: OAnalysis
    },
    {
      path: '/home/dataSearch',
      name:'/home/dataSearch',
      meta: {
        requireAuth: true
      },
      component: DataSearch,
      children: [
      {
        path: '/home/dataSearch/parkInfo',
        name:'/home/dataSearch/parkInfo',
        meta: {
          requireAuth: true
        },
        component: ParkInfo
      },
      {
       path: '/home/dataSearch',
       redirect: '/home/dataSearch/parkInfo'
     }
      ]
    },
    {
      path: '/home/testF',
      name:'/home/testF',
      meta: {
        requireAuth: true
      },
      component: TestF
    },
    {
      path: '/home/sysetsmM',
      name: '/home/sysetsmM',
      meta: {
        requireAuth: true
      },
      component: SysetmM,
      children: [
      {
        path: '/home/sysetsmM/menuManage',
        name: '/home/sysetsmM/menuManage',
        meta: {
          requireAuth: true
        },
        component: MenuManage
      },
      {
        path: '/home/sysetsmM/buttonManage',
        name: '/home/sysetsmM/buttonManage',
        meta: {
          requireAuth: true
        },
        component: ButtonManage
      },
      {
        path: '/home/sysetsmM/modelManage',
        name: '/home/sysetsmM/modelManage',
        meta: {
          requireAuth: true
        },
        component: ModelManage
      },
      {
        path: '/home/sysetsmM/userManage',
        name: '/home/sysetsmM/userManage',
        meta: {
          requireAuth: true
        },
        component: UserManage
      },
      {
        path: '/home/sysetsmM/divisionManage',
        name: '/home/sysetsmM/divisionManage',
        meta: {
          requireAuth: true
        },
        component: DivisionManage
      },
      {
        path: '/home/sysetsmM/roleManage',
        name: '/home/sysetsmM/roleManage',
        meta: {
          requireAuth: true
        },
        component: RoleManage
      },
      {
       path: '/home/sysetsmM',
       redirect: '/home/sysetsmM/menuManage'
     }
     ]
   },
   {
     path: '/home',
     redirect: '/home/homePage'
   }]
 },
 {
  path:'/login',
  name:'/login',
  component: Login
}
]


